import React, { useState, useEffect } from 'react';
import { Calendar, Clock, Loader2, ExternalLink, AlertCircle } from 'lucide-react';

type NewsItem = {
  id: string;
  title: string;
  link: string;
  content: string;
  content_snippet?: string;
  pub_date?: string;
  creator?: string;
  categories?: string[];
  image_url?: string;
};

export function News() {
  const [newsItems, setNewsItems] = useState<NewsItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchNews = async () => {
    try {
      // Fetch RSS feed
      const proxyUrl = 'https://api.allorigins.win/raw?url=' + encodeURIComponent('https://techcrunch.com/category/artificial-intelligence/feed/');
      const response = await fetch(proxyUrl);
      if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
      const text = await response.text();

      // Parse the XML
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(text, 'text/xml');
      const items = xmlDoc.getElementsByTagName('item');

      // Debug: Log first item's raw XML
      const serializer = new XMLSerializer();
      if (items.length > 0) {
        console.log('=== RAW FIRST ITEM ===', serializer.serializeToString(items[0]));
      }

      const newsArray: NewsItem[] = await Promise.all(
        Array.from(items).slice(0, 3).map(async (item, index) => {
          const link = item.getElementsByTagName('link')[0]?.textContent || '#';
          let imageUrl = 'https://imagedelivery.net/SZohZ5hDHQrPE1IYbmlzaA/dd1f5ea3-c3de-4dbd-b66b-1d63f739ac00/public';

          // Fetch article page for image
          try {
            const articleResponse = await fetch(`https://api.allorigins.win/raw?url=${encodeURIComponent(link)}`);
            if (articleResponse.ok) {
              const articleText = await articleResponse.text();
              const articleDoc = parser.parseFromString(articleText, 'text/html');
              const ogImage = articleDoc.querySelector('meta[property="og:image"]');
              if (ogImage) {
                imageUrl = ogImage.getAttribute('content') || imageUrl;
              }
            }
          } catch (err) {
            console.error(`Failed to fetch image for ${link}:`, err);
          }

          return {
            id: index.toString(),
            title: item.getElementsByTagName('title')[0]?.textContent || 'No title',
            link: link,
            content: item.getElementsByTagName('description')[0]?.textContent || 'No content',
            pub_date: item.getElementsByTagName('pubDate')[0]?.textContent || '',
            creator: item.getElementsByTagName('dc:creator')[0]?.textContent || '',
            image_url: imageUrl
          };
        })
      );

      console.log('=== HEY LOOK HERE === News items:', newsArray);
      setNewsItems(newsArray);
      setError(null);
    } catch (err) {
      console.error('Error fetching news:', err);
      setError('Failed to load news—check the console!');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchNews();
  }, []);

  function getReadingTime(content: string): string {
    const wordsPerMinute = 200;
    const words = content.split(/\s+/).length;
    const minutes = Math.ceil(words / wordsPerMinute);
    return `${minutes} min read`;
  }

  function formatDate(dateString: string | undefined): string {
    if (!dateString) return '';
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  }

  function truncateText(text: string, maxLength: number): string {
    if (!text) return '';
    const plainText = text.replace(/<[^>]+>/g, '');
    if (plainText.length <= maxLength) return plainText;
    return plainText.substr(0, maxLength).trim() + '...';
  }

  const handleArticleClick = (url: string) => {
    window.open(url, '_blank', 'noopener,noreferrer');
  };

  const renderContent = () => {
    if (loading) {
      return (
        <div className="flex justify-center items-center min-h-[400px]">
          <Loader2 className="w-12 h-12 text-white animate-spin" />
        </div>
      );
    }

    if (error) {
      return (
        <div className="bg-white/90 backdrop-blur-sm border border-red-200 rounded-lg p-6 text-center">
          <AlertCircle className="w-12 h-12 text-red-500 mx-auto mb-4" />
          <p className="text-red-600 mb-4">{error}</p>
        </div>
      );
    }

    return (
      <>
        {newsItems.length === 0 ? (
          <div className="text-center bg-white/90 backdrop-blur-sm rounded-lg p-8">
            <p className="text-gray-800 mb-4">No news articles available at the moment.</p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {newsItems.map((item) => (
              <article 
                key={item.id} 
                className="bg-white rounded-lg shadow-lg overflow-hidden transform hover:scale-105 transition-transform duration-300 cursor-pointer"
                onClick={() => handleArticleClick(item.link)}
              >
                <div className="relative h-48">
                  <img
                    src={item.image_url}
                    alt={item.title}
                    className="w-full h-full object-cover"
                    onError={(e) => { e.currentTarget.src = 'https://imagedelivery.net/SZohZ5hDHQrPE1IYbmlzaA/dd1f5ea3-c3de-4dbd-b66b-1d63f739ac00/public'; }}
                  />
                </div>
                <div className="p-6">
                  <div className="flex items-center text-sm text-gray-500 mb-4">
                    <Calendar className="w-4 h-4 mr-2" />
                    <span>{formatDate(item.pub_date)}</span>
                    <Clock className="w-4 h-4 ml-4 mr-2" />
                    <span>{getReadingTime(item.content)}</span>
                  </div>
                  <h2 className="text-xl font-bold mb-3 text-gray-900">{item.title}</h2>
                  <p className="text-gray-600 mb-4">
                    {truncateText(item.content_snippet || item.content, 150)}
                  </p>
                  {item.creator && (
                    <p className="text-sm text-gray-500 mb-4">
                      By {item.creator}
                    </p>
                  )}
                  <button 
                    onClick={(e) => {
                      e.stopPropagation();
                      handleArticleClick(item.link);
                    }}
                    className="text-blue-600 font-semibold flex items-center hover:text-blue-800 transition-colors"
                  >
                    Read on TechCrunch <ExternalLink className="w-4 h-4 ml-2" />
                  </button>
                </div>
              </article>
            ))}
          </div>
        )}
      </>
    );
  };

  return (
    <div className="min-h-screen bg-gray-100">
      <div className="relative py-20 px-4">
        <div className="absolute inset-0 bg-cover bg-center bg-no-repeat"
             style={{
               backgroundImage: `url('https://imagedelivery.net/SZohZ5hDHQrPE1IYbmlzaA/dd1f5ea3-c3de-4dbd-b66b-1d63f739ac00/public')`,
               backgroundPosition: 'center',
               backgroundSize: 'cover'
             }}>
          <div className="absolute inset-0 bg-black/50"></div>
        </div>
        
        <div className="max-w-6xl mx-auto relative z-10">
          <div className="text-center mb-16">
            <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold mb-4 text-white drop-shadow-[0_2px_2px_rgba(0,0,0,0.8)]">
              Latest AI News from TechCrunch
            </h1>
            <p className="text-xl text-gray-200 max-w-2xl mx-auto drop-shadow-[0_1px_1px_rgba(0,0,0,0.8)]">
              Stay updated with the latest developments in artificial intelligence
            </p>
          </div>

          {renderContent()}
        </div>
      </div>
    </div>
  );
}